.container{
    animation: fadeIn 0.7s ease-in-out;
    display: flex;
    align-items: center;
    width:100%;
    height: 80vh;
    margin: 0 auto;
    position: relative;
    z-index: 5;
    transition: height 3s ease-in-out;
}


.thisOne{
    width: 100%;
    height: 100vh;
}

.container2{
    width:100%;
    height: 100vh;
    transition: height 3s ease-in-out;
}




.video_container{
    height: 100%; /* 화면 전체 높이에 맞춤 */
    width: 100%;
    transition: height 3s ease-in-out,margin 3s ease-in-out;

}

.video_container__video{
    position: relative;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: 2;
}

.new__video_container__video{
    position: relative;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: 2;

}
.new__video_container{
    position: relative;
    width:100%;
    margin: 0 auto;
    height: 70vh;
    display: flex;
    align-items: center;
    object-fit: cover;
    z-index: 2;
    transition: height 2.5s ease-in-out,margin 2s ease-in-out;
}


@keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @media (max-width: 800px) {
    .video_container{
        height: 30vh; /* 화면 전체 높이에 맞춤 */
        width: 100%;
        transition: all 1s ease-in-out;
    }

    .container2{
        transition: all 3s ease-in-out;
        margin-bottom: 5vw;
        width:100%;
        height: 100%;
    }
  }

